:root {
  --main-bg-color: #2b2b2b;
  --border-color: #808080;
  --text-color: #f0f0f0;
}

@font-face {
  font-family: 'JetBrainsMono';
  src: local('JetBrainsMono'), url('./fonts/JetBrainsMono-VariableFont_wght.ttf') format('truetype');
}

#me {
  min-height: 100vh;
  height: 100%;
}

body {
  margin: 0;
  background-color: var(--main-bg-color);
  font-family: JetBrainsMono;
  font-weight: 100;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h5, p {
  color: var(--text-color);
}

a {
  font-size: .8rem;
  text-decoration: none;
  padding: 3px;
}

a:focus {
  outline: none;
}

a:visited {
  color: var(--border-color);
}

span {
  padding: 3px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

aside {
  width: 100%;
  max-width: 300px;
}

section {
  width: 100%;
}

ul {
  list-style-type: none;
  display: flex;
  flex-direction: row;
  align-items: center;
  flex-wrap: wrap;
  padding-left: 0;
  margin: 5px 0;
}

footer {
  margin-top: 1rem;
}
